@use './colors' as *;
@use './mixin' as *;
@use './breakpoints' as *;

.pagination {
    @include flex-center();
    @include font-sh(18px,21px);
    flex-wrap: nowrap;
    margin-top: 38px;
    margin-bottom: 28px;

    & &__active {
        background: map-get($colors,main-color);
        color:map-get($colors,white);
      }
    
    &__reference-direction {
        @include flex-center;

        height: 40px;
        width: 40px;
        border-radius: 50%;
        background: map-get($colors,gray);
      }
    
    &__pages {
        display: flex;
        align-items: center;
        justify-content: space-around;
        margin: 0px 17px;
        height: 40px;
        width: 360px;
        background: map-get($colors,gray);
        border-radius: 20px;
        align-items: center;
      }
    
    &__container {
        @include flex-center();
        width: 40px;
        height: 40px;
        border-radius: 50%;
      }

    @media (max-width: map-get($breakpoints, lg)) {
        &__pages {
            width: 80%;
            margin: 0px;
        }
    

    }

    @media (max-width: map-get($breakpoints, md)) {
        &__pages {
            width: 80%;
            margin: 0px;
        }
    
    }

    @media (max-width: map-get($breakpoints, sm)) {
        & {
            font-size: 14px; 
        }
      
        &__pages {
            width: 80%;
            margin: 0px;
        }
        &__ref-direction {
            height: 20px;
            width: 20px;
        }
    }
}